
import { Item } from "@/interface";
import { defineComponent } from "vue";

export default defineComponent({
  props: ["orderList", "totalAmount"],
  emits: ["showOrderList", "showOrderListModal"],
  computed: {
    totalQty() {
      let result = 0;
      this.orderList.forEach((item: Item) => (result += item.added));
      return result;
    },
  },
  methods: {
    showOrderList() {
      this.$emit("showOrderList", true);
    },
    showOrderListModal(value: boolean) {
      this.$emit("showOrderListModal", value);
    },
  },
});
